.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5em;
  width: 100%;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

:root {
  --col__base: hsl(0deg, 0%, 100%);
  --col__accent: hsl(176deg, 68%, 64%);
  --col__accent2: hsl(198deg, 60%, 50%);
  --col__error: hsl(0deg, 100%, 63%);
  --col__bg: hsl(218deg, 28%, 13%);
  --col__bg--footer: hsl(216deg, 53%, 9%);
  --col__bg--testimonial: hsl(219deg, 30%, 18%);
  --font__base: "Open Sans", sans-serif;
  --font__headings: "Raleway", sans-serif;
  --txt__base: 1.6rem;
  --txt__base--small: 1.4rem;
  --sp-sides: 6%;
}
@media (min-width: 768px) {
  :root {
    --txt__base: 2.4rem;
    --txt__base--small: 2rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
blockquote,
figure,
form,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
p,
pre {
  margin: 0;
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
  list-style: none;
}

button {
  border: 0;
  background: unset;
  color: currentColor;
  cursor: pointer;
}

body {
  color: var(--col__base);
  background-color: var(--col__bg);
  font-family: var(--font__base);
  font-size: var(--txt__base);
  line-height: 3.2rem;
}
body main > section,
body > header,
body > footer {
  padding: 1em var(--sp-sides);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font__headings);
  font-weight: 700;
}

h1 {
  font-size: 3rem;
  line-height: 5rem;
}

a {
  color: var(--col__base);
  text-decoration: none;
}
@media (hover: hover) and (pointer: fine) {
  a:hover, a:focus {
    text-decoration: underline;
  }
}

p {
  padding: 1em 0;
}

#svg-sprite {
  display: none;
}

.icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 3rem;
  height: 0;
  padding-top: 3rem;
}
.icon.smaller {
  width: 2rem;
  padding-top: 2rem;
}
.icon.small {
  width: 2.5rem;
  padding-top: 2.5rem;
}
.icon.big {
  width: 5rem;
  padding-top: 5rem;
}
.icon.bigger {
  width: 8rem;
  padding-top: 8rem;
}
.icon svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.btn {
  display: inline-block;
  margin: 2rem auto;
  padding: 1em 2em;
  background: linear-gradient(to right, var(--col__accent), var(--col__accent2) 70%);
  border-radius: 10rem;
  font-family: var(--font__headings);
  font-weight: 700;
  text-align: center;
}
@media (hover: hover) and (pointer: fine) {
  .btn:hover, .btn:focus {
    background: var(--col__accent);
    text-decoration: none;
  }
}

.copy {
  width: 100%;
  padding: 0 var(--sp-sides);
  justify-content: center;
  gap: 2em;
  background: white;
  font-size: var(--txt__base--small);
}
.copy, .copy a {
  color: black;
}
.copy p {
  padding: 0;
}
.copy li {
  padding-left: 0.5em;
  display: inline-block;
}
.copy i.fa-linkedin {
  color: #0077b5;
}

.l-hero {
  text-align: center;
  background: var(--col__bg) url("images/bg-curvy-mobile.svg") bottom 150% 50% no-repeat;
}
@media (min-width: 768px) {
  .l-hero {
    background-image: url("images/bg-curvy-desktop.svg");
    background-size: 120%;
  }
}
.l-hero .logo {
  width: 11rem;
  padding-bottom: 3rem;
}
.l-hero nav a {
  margin-left: 1.5em;
}
.l-hero__content {
  max-width: 70rem;
  margin: 2em auto;
}
.l-hero .btn {
  min-width: 35rem;
}

.info__icons {
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
  gap: 5em;
  align-items: center;
  text-align: center;
}
.info__icons article {
  max-width: 40rem;
}
.info__icons img {
  height: 11.5rem;
  margin: 5rem auto;
}
.info article.flex {
  width: 100%;
  flex-wrap: wrap;
  margin-top: 5em;
  gap: 5em;
}
@media (min-width: 768px) {
  .info article.flex {
    align-items: center;
    justify-content: space-between;
  }
}
.info article.flex a {
  padding-bottom: 0.5em;
  border-bottom: 2px solid currentColor;
  color: var(--col__accent);
}
@media (hover: hover) and (pointer: fine) {
  .info article.flex a:hover, .info article.flex a:focus {
    color: var(--col__base);
    text-decoration: none;
  }
}
.info article.flex i {
  margin-left: 0.5em;
}
.info article.flex div {
  flex: 1 1 30rem;
}

.testimonials {
  margin-top: 10rem;
  gap: 2em;
  flex-wrap: wrap;
}
.testimonials figure {
  min-width: 40rem;
  padding: 2em 2em 2em 3em;
  background-color: var(--col__bg--testimonial);
  border-radius: 1rem;
  box-shadow: 3px 3px 6px var(--col__bg--footer);
}
@media (min-width: 768px) {
  .testimonials figure {
    flex: 1 1 30rem;
  }
}
.testimonials blockquote {
  position: relative;
}
.testimonials blockquote:before {
  content: "";
  display: block;
  position: absolute;
  top: -1rem;
  left: -4rem;
  width: 3.5rem;
  height: 2.5rem;
  background-image: url("images/bg-quotes.png");
  background-size: cover;
}
.testimonials footer {
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.testimonials img {
  width: 5rem;
  margin-right: 1em;
  border-radius: 50%;
}
.testimonials cite {
  font-style: normal;
  flex: 1;
  line-height: 1.2em;
}

#footer {
  margin-top: 25rem;
  padding: 3em var(--sp-sides);
  position: relative;
  gap: 3em;
  flex-direction: column;
  flex-wrap: wrap;
  background: var(--col__bg--footer);
}
@media (min-width: 768px) {
  #footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: unset;
  }
}
#footer .email {
  position: absolute;
  width: 90%;
  top: -15%;
  left: 50%;
  transform: translateX(-50%);
  padding: 2em;
  background: var(--col__bg);
  border: 1px solid var(--col__bg--footer);
  border-radius: 1rem;
  box-shadow: 3px 3px 6px var(--col__bg--footer);
  text-align: center;
}
@media (min-width: 768px) {
  #footer .email {
    max-width: 70rem;
    top: -20%;
  }
}
#footer .email form {
  flex-direction: column;
  position: relative;
  width: 100%;
  gap: 2em;
}
#footer .email input {
  width: 100%;
  max-height: 5em;
  padding: 1em 2em;
  border: 0;
  border-radius: 10rem;
  font-size: 2rem;
  text-align: center;
}
#footer .email .btn {
  font-size: 2rem;
  margin: 0;
  width: 100%;
}
#footer .brand {
  margin-top: 25rem;
  width: 100%;
}
#footer > p {
  flex: 1 1 30%;
}
#footer > p i {
  padding-right: 1em;
}
#footer .icon {
  width: 24rem;
  padding-bottom: 6rem;
}
#footer ul {
  align-self: flex-start;
}
#footer li {
  padding: 0.5em 0;
}
#footer .social {
  margin: 0 auto;
  text-align: center;
  align-self: center;
}
#footer .social li {
  margin: 0 0.2em;
  display: inline-block;
}
#footer .social li a {
  width: 2em;
  height: 2em;
  justify-content: center;
  border: 1px solid currentColor;
  border-radius: 50%;
  text-decoration: none;
}
@media (hover: hover) and (pointer: fine) {
  #footer .social li a:hover, #footer .social li a:focus {
    color: var(--col__accent);
  }
}

/* Main layout, semantic tags. Use ID and classes prefixed as l- */